import dividLine from '@/components/line.vue';
import scrollIndex from '@/components/scroll-index.vue';
import mediaList from '@/components/mediaList.vue'
import goodList from '@/components/good-list.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
import goTop from '@/components/go-top.vue';
import uniIcon from '@/components/uni-icon.vue';
import uniGrid from '@/components/uni-grid.vue';
import {
	ztkActivify,
} from '@/api/goods.js'
export default {
	components: {
		dividLine,
		scrollIndex,
		mediaList,
		goodList,
		uniLoadMore,
		goTop,
		uniIcon,
		uniGrid
	},
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			isShow: false,
			page: 2,
			newsitems: new Object(),
			loadingType: 0,
			loadingTitle: "正在玩命加载...",
			mask: true,
			//条件筛选
			active: 0,
			isup: 3,
			sorts: new Object(),
			isDefault: true, //默认排序
			isTotal_sales: 0, //销量排序
			isTk_rate: 0, //佣金排序
			isPrice: 0, //价格排序
			sortTabFixed: false,
			pages: new Array(),
			type: "",
		}
	},
	onLoad(option) {
		uni.setNavigationBarTitle({
			title:option.type
		})
		this.type = option.type;
		uni.showLoading({
			title: "正在玩命加载..."
		})
		//初始化数据
		this.randomfn();
		setTimeout(function() {
			uni.hideLoading()
		}, 2000)
	},
	onNavigationBarButtonTap(e) {
		console.log(e);
		if (e.index == 0) {
			uni.navigateTo({
				url: '/pages/index/board'
			})
		}
	},
	onNavigationBarSearchInputClicked() {
		uni.switchTab({
			url: '/pages/common/search'
		})
	},
	methods: {
		touchmove(e) {
			console.log(uni.getStorageSync("nextPage" + this.type));
			if (!uni.getStorageSync("nextPage" + this.type)) {
				this.page++;
				console.log(this.page);
				ztkActivify({
					page: this.page,
					type: this.type
				}).then(res => {
					//this.newsitems[index].data = this.newsitems[index].data.concat(res.content);
					uni.setStorageSync("nextPage" + this.type, res.content);
					
					if (res.content < 10) {
						this.loadingType = 2;
						return;
					}
				})
			}
		},
		loadMore(e) {
			setTimeout(() => {
				console.log(this.newsitems);
				console.log(this.newsitems.concat(uni.getStorageSync("nextPage" + this.type)));
				this.newsitems = this.newsitems.concat(uni.getStorageSync("nextPage" + this.type));
				uni.removeStorageSync("nextPage" + this.type);
			}, 300);
		},
		randomfn() {
			ztkActivify({
				page: this.page,
				type: this.type
			}).then(res => {
				console.log(res);
				this.newsitems = res.content;
			});
		}
	},
}
